/* montserrat-100 - latin_cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 100;
  src: url("../fonts/montserrat-v18-latin_cyrillic-100.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("../fonts/montserrat-v18-latin_cyrillic-100.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/montserrat-v18-latin_cyrillic-100.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/montserrat-v18-latin_cyrillic-100.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/montserrat-v18-latin_cyrillic-100.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/montserrat-v18-latin_cyrillic-100.svg#Montserrat")
      format("svg"); /* Legacy iOS */
}
